.filter-box {
  width: 80%;
  z-index: 2;
  background-color: var(--color-white);
  position: fixed;
  top: 5rem;
  transform: translateY(-50%);
  border-radius: 0.25rem;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hidden {
  display: none;
}
